import { Carousel } from "react-bootstrap";
import { useGetLatestCollectionItemsQuery } from "../../app/collection-items/collection-items.api-slice";
import CollectionItem from "../collection/collection-item/Collection-item";

function LatestCollectionItemsCarousel() {
  const { data: items = [] } = useGetLatestCollectionItemsQuery(5);
  if (!items || !items.length) return null;
  return (
    <Carousel indicators={false} variant="dark">
      {items.map((item) => (
        <Carousel.Item key={item.id}>
          <CollectionItem item={item} />
        </Carousel.Item>
      ))}
    </Carousel>
  );
}

export default LatestCollectionItemsCarousel;
